import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Link, BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { Navbar, Nav } from "react-bootstrap";

import "./App.css";

import Home from "./Home";
import Contact from "./Contact";
import Exhibitions from "./Exhibitions";
import logo from "./assets/mmxx_logo.png";

function App() {
  return (
    <div>
      <Router basename="MMXX">
        <Navbar bg="white" expand="lg">
          <Navbar.Brand href="/MMXX">
            <img src={logo} alt="" width="36px" height="20px" />{" "}
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="mr-auto">
              <Nav.Link href="/zuccheri" as={Link} to="/zuccheri">
                Exhibition,
              </Nav.Link>
              <Nav.Link href="/contact" as={Link} to="/contact">
                Contact
              </Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
        <Switch>
          <Route path="/" exact>
            <Home />
          </Route>

          <Route path="/contact">
            <Contact />
          </Route>
          <Route path="/zuccheri">
            <Exhibitions title={"Luigi Zuccheri"} />
          </Route>
          <Route path="/exhibition2">
            <Exhibitions title={"Exhibition 2"} />
          </Route>
          <Route path="/exhibition3">
            <Exhibitions title={"Exhibition 3"} />
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
